.svgFooter{
    position: relative;
    padding-bottom: 0;
}

.svg{
    clip-path: polygon(0% 100%, 16.86% 20%, 63.72% 58%, 73.15% 69%, 77.1% 39.01%, 89.01% 65.01%, 92.99% 51.34%, 100% 100px, 100% 100%);
    background-color:#009279;
    width: 100%;
    height: 100px;
}

Footer{
    background: linear-gradient(to top, #006b3b, #009279);
    color: beige;
    height: 300px;
    padding: 1rem;
}

.Footer{
    display: flex;
    align-items: center;
}

.formFooter{
    width: 100%;
}

.formRow{
    display: flex;
}

.formText{
    width: 100%;
}